<template>
	<view>
		
		<uni-table border stripe emptyText="暂无更多数据">
			<!-- 表头行 -->
			<uni-tr>
				<uni-th align="center">名称</uni-th>
				<uni-th align="center">价格</uni-th>
				<uni-th align="center">操作</uni-th>
			</uni-tr>
			<!-- 表格数据行 -->
			<uni-tr v-for="(item,index) in leimulist" :key="index">
				<uni-td align="center">{{item.cate.name}}</uni-td>
				<uni-td align="center">{{item.cate.price}}</uni-td>
				<uni-td align="center">
					<button class="mini-btn" type="primary" size="mini" @click="open(item)">服务品牌</button>
				</uni-td>
			</uni-tr>

		</uni-table>
		<!-- 选择品牌弹窗 -->
		<uni-popup ref="popup" type="bottom">
			<view class="tachuang">
				<view class="title">请配置服务品牌</view>
				<view class="titletext">
					<view class="">服务名称：{{namepar}}</view>
					<view class="">价格：{{par}}</view>
					<view class="oktianjia"><button class="mini-btn" type="primary" size="mini" @tap="addcar()">确认添加</button></view>
				</view>
				<view class="bodybig">
					<!-- 左边 -->
					<scroll-view scroll-y="true" class="leftbod">
						<view class="inp"><input type="text" placeholder="请输入需要添加的车品牌"></view>
						<view ><!--  @tap="pingpaitap(index,item)" :class="{blue:index==carindex}" {{item.name}} -->
						<checkbox-group name="" @change="danxuanpingpai">
							<view class="" v-for="(item,index) in allcar" :key="index"  @tap="pingpaitap(index,item)" :class="{blue:index==carindex}">
								<!-- <label style="position: absolute;left: 5rpx;top: 50%;transform: translateY(-50%);">
									<checkbox :value="item.brand_id" @click.stop.native="()=>{}"/><text></text>
								</label> -->
								{{item.name}}
							</view>
						</checkbox-group>
						</view>
					</scroll-view>
					<!-- 右边 -->
					<scroll-view scroll-y="true" class="rightbod">
						<view>
						<uni-table ref="table" border stripe emptyText="暂无更多数据" type="selection" @selection-change="selectionChange" >
							<!-- 表头行 -->
							<uni-tr>
								<uni-th align="center">车系名称</uni-th>
							</uni-tr>
							<!-- 表格数据行 -->
							<uni-tr v-for="(item,index) in carlist" :key="index">
								<uni-td align="center">{{item.name}}</uni-td>
							</uni-tr>
						
						</uni-table>
							
						</view>
					</scroll-view>
				</view>
			</view>
		</uni-popup>
		
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				//类目列表
				leimulist:[],
				//选中的数据
				okdata:[],
				//选中的名称
				namepar:null,
				//选中的价格
				par:null,
				//选中的id
				xid:null,
				//全部车品牌
				allcar:[],
				//车品牌的数字下标
				carindex:0,
				//汽车品牌
				carlist:[],
				
				//选中的品牌id
				pinpaiid:[],
				//选中的系列id
				xilieid:[],
				
			}
		},
		onLoad() {

			this.getleimu()
		},
		methods: {
			//单选车品牌单选框触发
			danxuanpingpai(e){
				this.pinpaiid = e.detail.value
				console.log('单选车品牌',this.pinpaiid)
				var pinshuzu = new Object()
			},

			//添加车
			async addcar(){
				if(this.xid != null && this.pinpaiid != '' && this.xilieid.length != 0){
					const req = await uni.$http.post('api/jiu/usercate/addcatepai',{
						id:this.xid,
						brand_ids:this.pinpaiid,
						mark_ids:this.xilieid
					})
					console.log('添加',req)
				}else{
					uni.showToast({
						icon:"none",
						title:'请选择品牌车系'
					})
				}
				
			},
			
			//点击车品牌
			pingpaitap(index,item){
				this.$refs.table.clearSelection()
				// this.xilieid = []
				console.log('删除了数组',this.xilieid)
				this.carindex = index
				console.log(index)
				this.carindex = index
				this.getcar(this.carindex)
			},
			
			//获取车品牌
			async getcar(){
				const req = await uni.$http.post('api/jiu/usercate/carpinpai',{
					id:this.xid
				})
				this.allcar = req.data.data
				console.log('车品牌id数据',this.allcar)
				this.carlist = this.allcar[this.carindex].child
				console.log('子代',this.carlist)
				// this.pinpaiid =  req.data.data[this.carindex].brand_id
				
				// console.log('这是',this.carlist)
				// console.log('车品牌',this.allcar)
			},
			
			//唤起弹窗
			 open(item){
				this.xid = item.id
				 this.okdata = item
				 this.namepar = item.cate.name
				 this.par = item.cate.price
				console.log(this.namepar)
			        // 通过组件定义的ref调用uni-popup方法 ,如果传入参数 ，type 属性将失效 ，仅支持 ['top','left','bottom','right','center']
			    this.$refs.popup.open('bottom')
				this.getcar()
			},
			
			//类目列表
			async getleimu(){
				const req = await uni.$http.post('api/jiu/usercate/carelist',{
					user_id:uni.getStorageSync('user_id')
				})
				// console.log(req)
				this.leimulist = req.data.data.action
			},
			
			// 多选
			selectionChange(e) {
				console.log(e.detail.index)
				var inar = e.detail.index
				var okarr = []
				for(let i in inar){
					inar[i]
					for(let k in this.carlist){
						this.carlist[k]
						if(inar[i] == k){
							okarr.push(this.carlist[k].seriesid)
						}
					}
				}
				
				this.xilieid = okarr
				console.log('选中的车',this.xilieid)
			},
			
		}
	}
</script>
<style >
	page{
		background-color: #f3f3f3;
	}
</style>

<style scoped lang="scss">
.tachuang{
	border-radius: 15rpx 15rpx 0rpx 0rpx; 
	width: 100%;
	height: 1000rpx;
	background-color: white;
	.title{
		width: 90%;
		text-align: center;
		margin: auto;
		font-weight: 600;
	}
	.titletext{
		width: 90%;
		margin: 20rpx auto;
		.oktianjia{
			position: absolute;
			right: 50rpx;
			top: 60rpx;
		}
	}
	.bodybig{
		width: 100%;
		display: flex;
		
		.leftbod{
			height: 800rpx;
			flex: 1;
			.inp{
				background-color: #e6e6e6;
			}
			view{
				position: relative;
				text-align: center;
				padding: 20rpx 0rpx;
				color: #626262;
			}
			.blue{
				color: #2892d3;
			}
		}
		
		.rightbod{
			height: 800rpx;
			flex: 1.5;
			// border: 1rpx solid black;
		}
	}
}
</style>
